
.app-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

@media (min-width: 768px) {
    /* PC�����̃X�^�C�� */
    .dial-container {
        position: relative;
        align-items: center;
        margin-bottom: 10px;
        margin-top: 0px;
    }

    .dial-container-top {
        width: 400px;
        height: 20px;
        border-color: #000;
        border-style: solid;
        border-top-width: 5px;
        border-left-width: 5px;
        border-right-width: 5px;
        border-bottom-width: 0px;
    }

    .dial {
        width: 80px;
        height: 80px;
        background-color: #000;
        border-radius: 50%;
        display: inline-block;
        margin: 10px;
        cursor: pointer;
        transition: transform 0.3s ease-in-out;
    }

    .tick-mark {
        position: absolute;
        top: 42%;
        left: 50%;
        width: 4px;
        height: 32px;
        background-color: #FFF;
        transform: translate(-50%, -100%) rotate(0deg);
        transform-origin: top;
    }

    .cross-marks {
        display: flex;
    }

    .cross-marks-container-top {
        border-color: #000;
        border-style: solid;
        border-top-width: 10px;
        border-left-width: 10px;
        border-right-width: 10px;
        border-bottom-width: 0px;
    }

    .cross-marks-container-bottom {
        border-color: #000;
        border-style: solid;
        border-top-width: 10px;
        border-left-width: 10px;
        border-right-width: 10px;
        border-bottom-width: 10px;
    }

    .cross {
        position: relative;
        display: inline-block;
        width: 40px;
        height: 5px;
        background: #000;
        transform: rotate(45deg);
        vertical-align: middle;
    }

        .cross::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #000;
            transform: rotate(90deg);
        }

    .ok-mark {
        position: relative;
        width: 30px;
        height: 30px;
        border-style: solid;
        border-width: 5px;
        border-color: #000;
        border-radius: 50%;
        display: inline-block;
        vertical-align: middle;
    }

    .space {
        position: relative;
    }

    .marks-container {
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .clear-message {
        background-color: antiquewhite;
        width: 320px;
        font-size: 30px;
        font-weight: bold;
        text-align: center;
        color: #27ae60;
    }

    .tweet-message {
        background-color: antiquewhite;
        width: 320px;
        font-size: 20px;
        text-align: center;
    }
}

